@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
html, body {
    margin:0;
    height:100%;
    font-family: 'Press Start 2P';
    color: #6a6778;
    background: url(/memory-dex/static/media/background.35588919.jpg) no-repeat center fixed;
    background-color: turquoise;     
    background-size: cover;
  }
#header {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-content: center;
    align-items: center;
}
#title {
    font-size: 2.5em;
    text-align: center;
}
#level {
    font-size: 2em;
    text-align: center;
}
#scoreBoard {
    display: flex;
    width: 30vw;
    flex-direction: column;
    background-repeat: no-repeat;
    background-size: 100% 100%;
    align-items: center;
    background-image: url(/memory-dex/static/media/scoreBackground.ee948f63.png);
    background-color: white;
    border-radius: 1em;
    font-size: 0.8em;
    background-repeat: no-repeat;
    background-size: 100% 100%;
}
@media (max-width: 1100px) {
    #scoreBoard {
        display: none
    }
    #title {
        font-size: 1em;
    }
    #level {
        font-size: 1em;
    }
}
@-webkit-keyframes grow {
    0% {
    -webkit-transform: scale(.3);
            transform: scale(.3);
    }
    60% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
        }
    100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    }
  }
@keyframes grow {
    0% {
    -webkit-transform: scale(.3);
            transform: scale(.3);
    }
    60% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
        }
    100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    }
  }
@-webkit-keyframes growClick {
    0% {
    -webkit-transform: scale(.3);
            transform: scale(.3);
    }
    60% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    }
    100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    }
}
@keyframes growClick {
    0% {
    -webkit-transform: scale(.3);
            transform: scale(.3);
    }
    60% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    }
    100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    }
}
#cardsContainer {
    display:flex;
    flex-direction: row;
    margin: 3vw;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}
.card {
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 1em;
    -webkit-animation-name: grow;
            animation-name: grow;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-iteration-count:1;
            animation-iteration-count:1;
    -webkit-animation-iteration-count:initial;
            animation-iteration-count:initial;
    margin: 1vw;
}
.cardClicked {
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 1em;
    -webkit-animation-name: growClick;
            animation-name: growClick;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-iteration-count:1;
            animation-iteration-count:1;
    -webkit-animation-iteration-count:initial;
            animation-iteration-count:initial;
    margin: 1vw;
}
.cardImg{
    width: 180px;
    height: 180px;
    background: url(/memory-dex/static/media/cardBackground.ddfebe8c.png) center;
    background-color: white;
    border-radius: 5px;
    cursor:pointer;
    padding: 1vw;
}

.card:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
.cardClicked:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}


@-webkit-keyframes loading {
    0% {-webkit-transform: rotate(-360deg);transform: rotate(-360deg)};
    100% {-webkit-transform: rotate(-360deg);transform: rotate(-360deg)};
}

@keyframes loading {
    0% {-webkit-transform: rotate(-360deg);transform: rotate(-360deg)};
    100% {-webkit-transform: rotate(-360deg);transform: rotate(-360deg)};
}

.loadingScreen {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: fixed;
    width: 100%;
    min-height: 100%;
    height: auto;
    top:0px;
    left:0px;
    background: url(/memory-dex/static/media/background.35588919.jpg) no-repeat center fixed;     
    background-size: cover;
}
#pokeFlex{
    display:flex;
    flex-direction: column;
    align-items: center;
}
#pokeball {
    width: 150px;
    height: 150px;
    -webkit-animation-name: loading;
            animation-name: loading;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}
#loadingText{
    font-size: 3em;
    text-align: center;
}
#psyduck {
    width: 200px;
    height: 200px;
}
#newGame{
    font-family: 'Press Start 2P';
    background-color: transparent;
    cursor: pointer;
    font-size: 1em;
    border-radius: 5px;
    padding: 5px;
    color: #6a6778;
    background-color: rgba(235, 235, 235, 0.897);
}
.newGameText{
    font-size: 1.5em;
    text-align: center;
}
